import React from 'react';
import ReactDOM from 'react-dom';
import 'bootstrap/dist/js/bootstrap.bundle.min';
import 'bootstrap/dist/css/bootstrap.css';
import './index.css';

function Square(props) {
  return (
    <button className="square" onClick={props.onClick}>
      {props.value}
    </button>
  );
}

class Board extends React.Component {
  renderSquare(i) {
    return (
      <Square
        value={this.props.squares[i]}
        onClick={() => this.props.onClick(i)}
      />
    );
  }

  render() {
    return (
      <div>
        <div className="board-row">
          {this.renderSquare(0)}
          {this.renderSquare(1)}
          {this.renderSquare(2)}
        </div>
        <div className="board-row">
          {this.renderSquare(3)}
          {this.renderSquare(4)}
          {this.renderSquare(5)}
        </div>
        <div className="board-row">
          {this.renderSquare(6)}
          {this.renderSquare(7)}
          {this.renderSquare(8)}
        </div>
      </div>
    );
  }
}

class Game extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      history: [
        {
          squares: Array(9).fill(null),
        },
      ],
      stepNumber: 0,
      xIsNext: true,
    };
  }

  handleClick(i) {
    const history = this.state.history.slice(0, this.state.stepNumber + 1);
    const current = history[history.length - 1];
    const squares = current.squares.slice();
    if (calculateWinner(squares) || squares[i]) {
      return;
    }
    squares[i] = this.state.xIsNext ? 'X' : '0';
    this.setState({
      history: history.concat([
        {
          squares: squares,
        },
      ]),
      stepNumber: history.length,
      xIsNext: !this.state.xIsNext,
    });
  }

  jumpTo(step) {
    this.setState({
      stepNumber: step,
      xIsNext: step % 2 === 0,
    });
  }

  render() {
    const history = this.state.history;
    const current = history[this.state.stepNumber];
    const winner = calculateWinner(current.squares);

    const moves = history.map((step, move) => {
      const desc = move ? 'Vá para mover #' + move : 'Vá para o início do jogo';
      return (
        <li key={move}>
          <button
            className="btn btn-danger shadow rounded-0 text-white mb-2 mt-1"
            onClick={() => this.jumpTo(move)}
          >
            {desc}
          </button>
        </li>
      );
    });

    let status;
    if (winner) {
      status = (
        <p>
          Vencedor: <span className="text-danger">{winner}</span>
        </p>
      );
    } else {
      status = 'Próximo jogador: ' + (this.state.xIsNext ? 'X' : '0');
    }

    return (
      <div className="bg-light">
        <div className="container">
          <div className="row d-flex justify-content-center">
            <div className="col text-center pb-3">
              <h1 className="fw-bolder">Jogo da velha online #</h1>
            </div>
          </div>
          <div className="row d-flex vh-100 justify-content-center align-items-center">
            <div className="col-6 bg-white shadow col-md-6 d-flex justify-content-center py-5">
              <div className="game">
                <div className="game-board">
                  <Board
                    squares={current.squares}
                    onClick={(i) => this.handleClick(i)}
                  />
                </div>
              </div>
            </div>
            <div className="col-6 col-md-6 d-flex justify-content-center">
              <div className="game-info">
                <h4 className={'mb-2 fw-bolder'}>{status}</h4>
                <ol>{moves}</ol>
              </div>
            </div>
          </div>
        </div>
        <div className="w-100 border-top border-dark  pt-3 d-flex justify-content-center align-items-center">
          <p>
            Desenvolvido por:{' '}
            <a href="http://eideveloper.com.br/">José Ben Hur</a>
          </p>
        </div>
      </div>
    );
  }
}

//===========================

ReactDOM.render(<Game />, document.getElementById('root'));

function calculateWinner(squares) {
  const lines = [
    [0, 1, 2],
    [3, 4, 5],
    [6, 7, 8],
    [0, 3, 6],
    [1, 4, 7],
    [2, 5, 8],
    [0, 4, 8],
    [2, 4, 6],
  ];
  for (let i = 0; i < lines.length; i++) {
    const [a, b, c] = lines[i];
    if (squares[a] && squares[a] === squares[b] && squares[a] === squares[c]) {
      return squares[a];
    }
  }
  return null;
}
